import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home.vue'
import moduleRoutes from './moduleRoutes'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home/main'
    },
    {
      path: '/',
      redirect: '/home/main',
      component: Home,
      children: moduleRoutes
    }
  ]
})



